import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { EffectsModule } from "@ngrx/effects";
import { UpdateUserComponent, UpdateUserDetailsComponent, UpdateUserAccessprivComponent } from './index';
import { UpdateUserRoutingModule } from './updateuser-routing.module';
import { ProfileSharedModule } from '../shared/shared.module';
import { SharedModule } from '../../shared/shared.module';
import { UserDetailsResolve } from '../../_resolvers'

@NgModule({
    imports: [
        CommonModule,
        HttpModule,
        FormsModule,
        ReactiveFormsModule,
        ProfileSharedModule,
        SharedModule,
        UpdateUserRoutingModule,
       ],
    declarations: [
        UpdateUserComponent,
        UpdateUserDetailsComponent,
        UpdateUserAccessprivComponent
      ],
    exports: [ UpdateUserComponent,UpdateUserDetailsComponent ],
    providers:[ UserDetailsResolve ]
})
export class UpdateUserModule { }



