import { Component, Input, Output, OnInit, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import * as fromRoot from '../../app-state/reducers';
import * as profileTypes from '../../app-state/actions/profile'
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/takeUntil';
import { Subject } from "rxjs/Subject";
import { ProfileService } from '../../_services'

@Component({
    templateUrl: './userdetails.component.html',
    styles: ['gme-status-box{margin-bottom:20px;}']
})
export class UpdateUserDetailsComponent implements OnInit, OnDestroy {

    userprofileDtls$: Observable<any>;
    firstName: string;
    lastName: string;
    userName: string;
    phoneNumber: string;
    phoneExtn: string;
    altPhoneNumber: string;
    altPhoneExt: string;
    faxNumber: string;
    email: string;
    securityRole: string;
    showUserType: boolean;
    customerList:any;
    userCategory:string;
    startStopAce: string;
    billingAce: string

    message: string;
    isSuccess: boolean;
    isUpdateSuccesful: boolean;
    showAccessPrivileges:boolean;

    //added for reset password component
    user: any;
    isUserNotSelectedForUpdate: boolean;

    destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private route: ActivatedRoute, private profileService: ProfileService, private store: Store<fromRoot.State>, private router: Router) {
        this.userprofileDtls$ = this.store.select(fromRoot.getUserProfileDetails);
    }

    onUpdateUser(inData){
        //console.log(inData)
        let user = this.getUserWithUpdatedValues(inData);
        let fieldName = (inData.fieldName == 'BA' || inData.fieldName == 'SA')?"privileges":inData.fieldName
        this.store.dispatch(new profileTypes.UpdateUser({fieldName:fieldName,user}))

        this.store.select(fromRoot.getUserUpdatedDtls).takeUntil(this.destroy$).subscribe(updUser => {
            this.isSuccess = updUser.userUpdated;
            if(updUser.userUpdated){
              if(inData.fieldName == 'BA' || inData.fieldName == 'SA'){
                  this.startStopAce = user.startStopAce;
                  this.billingAce = user.billingAce;
              }
              this.user = user;
            }
            //this.message = updUser.userUpdated?"Update "+updUser.fieldName+" Successful":"Update "+updUser.fieldName+" Failed"
            this.message = updUser.userUpdated?"Profile update was successful.":"There was a problem with you profile update."
            this.isUpdateSuccesful = updUser.userUpdated;
        })
    }

    onBrokerAccesspriv(accessprivileges: any){
        let startStopAce = accessprivileges.startStopAccess?"Y":"N";
        let billingAce = accessprivileges.paybillAccess?"Y":"N";
        let user = Object.assign({},this.user);
        this.store.dispatch(new profileTypes.UpdateUser({fieldName:"brokerprivileges",user,
        bpNumber:accessprivileges.bpNumber,startStopAce,billingAce}))
        this.customerList = this.profileService.setBrokerPermissions(accessprivileges,this.customerList)
    }

    resetPasswordStatus(status){
        this.isUserNotSelectedForUpdate = status.isUserNotSelectedForUpdate;
        this.message = status.message;
        this.isSuccess = status.isSuccess;
    }

    onBack(){
        this.router.navigate(['/protected/customer/profile/update/user.htm']);
    }

    onRemoveUser(){
        this.store.dispatch(new profileTypes.SaveUserNameForProfileDetails(this.userName));
        this.router.navigate(['/protected/customer/profile/remove/userverify.htm']);
    }

    checkForAccessPrivileges(){
       this.showAccessPrivileges = this.profileService.checkForAccessPrivilegesForUserDetails(this.securityRole);
    }

    getUserWithUpdatedValues(inData: any){
        let user = Object.assign({},this.user);
        if(inData.fieldName == 'name'){
            user.firstName = inData.firstName;
            user.lastName = inData.lastName;
        }else if(inData.fieldName == 'phone'){
            user.phoneNum = inData.phoneNum;
            user.phoneNumExtn = inData.phoneNumExtn;
        }else if(inData.fieldName == 'altphone'){
            user.altPhoneNum = inData.altPhoneNum;
            user.phoneExtnAlt = inData.phoneExtnAlt;
        }else if(inData.fieldName == 'fax'){
            user.faxNum = inData.faxNum;
        }else if(inData.fieldName == 'email'){
            user.email = inData.email;
        }else if(inData.fieldName == 'password'){
            user.password = inData.password;
        }else if(inData.fieldName == 'SA'){
            user.startStopAce = inData.startStopAce;
            user.billingAce = inData.billingAce;
        }else if(inData.fieldName == 'BA'){
            user.startStopAce = inData.startStopAce;
            user.billingAce = inData.billingAce;
        }
        return user;
    }

    ngOnInit() {

      let userDetails = this.route.snapshot.data['userDetails'];
      let user = userDetails.user;
      this.user = user;
      this.customerList = this.profileService.processLoggedInCustomerListForAddUser(userDetails.customerList);
      this.firstName = user.firstName;
      this.lastName = user.lastName;
      this.userName = user.userName;
      this.email = user.email;
      this.phoneNumber = user.phoneNum;
      this.phoneExtn = user.phoneNumExtn;
      this.altPhoneNumber = user.altPhoneNum;
      this.altPhoneExt = user.altPhoneNumExtn;
      this.faxNumber = user.faxNum;
      this.securityRole = user.webSecurityRole;
      this.showUserType = false;
      this.startStopAce = user.startStopAce;
      this.billingAce = user.billingAce;
      this.userCategory = user.userCategory;
      this.checkForAccessPrivileges();
    }

    ngOnDestroy(){
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }

}



