import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UpdateUserComponent, UpdateUserDetailsComponent, } from './index';
import { UserDetailsResolve } from '../../_resolvers'

const routes: Routes = [
  { path: 'user.htm', component: UpdateUserComponent },
  { path: 'userdetails.htm',
    component: UpdateUserDetailsComponent,
    resolve: {
      userDetails: UserDetailsResolve
    }}
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class UpdateUserRoutingModule { }



